@import url(https://fonts.googleapis.com/css2?family=Zen+Tokyo+Zoo&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  height: 100vh;
  width: 100vw;
  background-image: url('https://blog.care32dental.com/wp-content/uploads/2016/10/triangle-geometric-background.png');
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
  font-family: 'Source Code Pro', monospace;
}

.header {
  width: 100%;
  height: 78px;
  padding: 10px 0 10px 10px;
}

.header h1 {
  font-family: 'Zen Tokyo Zoo', cursive;
  letter-spacing: 3px;
  font-size: 3rem;
  text-decoration: underline;
}

.grids {
  height: calc(100vh - 78px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.col {
  display: flex;
  flex-direction: column;
}

.cell {
  height: 150px;
  width: 170px;
  margin: 2px;
  background-color: rgb(12, 3, 3);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 1.5rem;
  cursor: pointer;
}

.cell:hover {
  opacity: 0.5;
  color: white;
}

.container {
  width: 85%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.input_container,
.input_container2 {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
  margin: 15px 0;
}

.input_container2 {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.input_container .inputs, .input_container2 input {
  height: 40px;
  width: 60px;
  font-size: 1.3rem;
  padding: 10px;
}

button {
  height: 50px;
  width: 90px;
  font-size: 1.2rem;
  border-width: 0.1vw;
  border-radius: 25px;
}

button:hover {
  background-color: #eee;
  color: black;
  box-shadow: 0 0 5px 2px #111;
  border: none;
}

.result {
  margin-top: 10px;
  font-size: 1.2rem;
}

.back {
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  align-items: center;
  font-size: 1.2rem;
  text-decoration: none;
  color: black;
  margin: 10px 0 0 10px;
  position: absolute;
  top: 0;
  left: 0;
}

.back:hover {
  border-bottom: 1px solid black;
  padding-bottom: 10px;
}

.text {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
  margin-top: 10px;
  font-weight: 600;
  text-decoration: underline;
}

.subtext {
  font-weight: 600;
  margin-top: 5px;
}

.quiz {
  height: 100vh;
  background-image: url('https://blog.care32dental.com/wp-content/uploads/2016/10/triangle-geometric-background.png');
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
  font-family: 'Source Code Pro', monospace;
  overflow-y: scroll;
}

.quiz_back {
  text-decoration: none;
  color: black;
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  align-items: center;
  font-size: 1.2rem;
  padding: 10px;
}

.quiz_back:hover {
  text-decoration: underline;
}

.quiz_container {
  margin-top: 10px;
  width: 85%;
  margin: 0 auto;
}

.title {
  text-align: center;
}

.questions {
  height: 100px;
  margin: 10px auto;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  border: 1px solid rgb(26, 25, 25);
  padding: 10px;
  border-radius: 5px;
  text-align: center;
}

.questions select {
  max-width: 70%;
  width: 160px;
  margin: 0 auto;
  height: 40px;
  padding-left: 10px;
}

.quiz_button {
  display: block;
  margin: 10px auto;
}

.result {
  margin-bottom: 20px;
  text-align: center;
}

.correct {
  background-color: green;
  opacity: 0.9;
}

.incorrect {
  background-color: red;
  opacity: 0.9;
}

.divider {
  display: block;
  text-align: center;
  margin: 10px 0;
}

